.container{
  font-family: 'Open Sans', sans-serif;
  display: flex;
  flex-direction: column;
}
.center{
  width: 50%;
  margin-top: 20px;
}
.name{
  width: 250px;
}
.item{
  width: 300px;
}
.qty{
  width:75px;
}
.row{
  margin-top: 10px;
}
.cross{
  color:grey;
}
.cross:hover{
  color:red;
  cursor: pointer;
}
.borderless td, .borderless th {
  border: none;
}
.plus:hover{
  color: green;
  cursor: pointer;
}
.minus:hover{
  color: red;
  cursor: pointer;
}
[class*="col-"] {
  width: 100%;
}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .col-1 {width: 8.33%;}
  .col-2 {width: 16.66%;}
  .col-3 {width: 25%;}
  .col-4 {width: 33.33%;}
  .col-5 {width: 41.66%;}
  .col-6 {width: 50%;}
  .col-7 {width: 58.33%;}
  .col-8 {width: 66.66%;}
  .col-9 {width: 75%;}
  .col-10 {width: 83.33%;}
  .col-11 {width: 91.66%;}
  .col-12 {width: 100%;}
}